.Searchbar {
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 30px;
  z-index: 1;
  width: 740px;
  // width: 480px;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  background-color: lightslategray;
  margin-top: 10px;
  margin-bottom: 20px;
  text-align: center;
  box-shadow: 8px 8px 0px rgba(0, 0, 0, 0.8);

  &__Form {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
  }

  &__Button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;

    &:hover {
      opacity: 1;
    }

    &Label {
      position: absolute;
      width: 1px;
      height: 1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      white-space: nowrap;
      clip-path: inset(50%);
      border: 0;
    }
  }

  &__Input {
    display: inline-block;
    width: 100%;
    font: inherit;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;

    &::placeholder {
      font: inherit;
      opacity: 0.5;
    }
  }
}
